.timeLine-slot-occupied {
  cursor: pointer;
  position: absolute;
  background: #d5dfe9;
  height: 100%; }
  .timeLine-slot-occupied:hover {
    background: #99a9b9; }
  .timeLine-slot-occupied:active {
    background: -webkit-gradient(linear, left top, left bottom, from(rgba(0, 0, 0, 0.12)), to(rgba(0, 0, 0, 0.12))), -webkit-gradient(linear, left top, left bottom, from(#98a9b9), to(#98a9b9));
    background: -webkit-linear-gradient(rgba(0, 0, 0, 0.12), rgba(0, 0, 0, 0.12)), -webkit-linear-gradient(#98a9b9, #98a9b9);
    background: -o-linear-gradient(rgba(0, 0, 0, 0.12), rgba(0, 0, 0, 0.12)), -o-linear-gradient(#98a9b9, #98a9b9);
    background: linear-gradient(rgba(0, 0, 0, 0.12), rgba(0, 0, 0, 0.12)), linear-gradient(#98a9b9, #98a9b9); }



