.timeLine-slot-free {
  cursor: pointer;
  position: absolute;
  background: #ffffff;
  height: 100%; }
  .timeLine-slot-free:hover {
    background: #1d54fe; }
    .timeLine-slot-free:hover::after {
      position: absolute;
      top: 50%;
      left: 50%;
      -webkit-transform: translate(-50%, -50%);
          -ms-transform: translate(-50%, -50%);
              transform: translate(-50%, -50%);
      content: " ";
      width: 12px;
      height: 12px;
      background: -webkit-linear-gradient(top, transparent 5px, #ffffff 5px, #ffffff 7px, transparent 7px), -webkit-linear-gradient(left, transparent 5px, #ffffff 5px, #ffffff 7px, transparent 7px);
      background: -o-linear-gradient(top, transparent 5px, #ffffff 5px, #ffffff 7px, transparent 7px), -o-linear-gradient(left, transparent 5px, #ffffff 5px, #ffffff 7px, transparent 7px);
      background: linear-gradient(to bottom, transparent 5px, #ffffff 5px, #ffffff 7px, transparent 7px), linear-gradient(to right, transparent 5px, #ffffff 5px, #ffffff 7px, transparent 7px); }
  .timeLine-slot-free:active {
    background: #1d00fe; }



