.editing-input {
  background: #ffffff;
  border: 2px solid #e9ecef;
  border-radius: 4px;
  padding-left: 10px;
  height: 38px;
  line-height: 38px;
  width: 100%;
  font-family: HelveticaNeue;
  font-size: 13px;
  color: #000000;
  letter-spacing: 0.47px; }
  @media screen and (max-width: 640px) {
    .editing-input {
      height: 44px;
      line-height: 44px;
      font-size: 15px; } }
  .editing-input:hover {
    border-color: #007dff; }
  .editing-input:focus {
    outline: none; }
  .editing-input:active {
    outline: none; }
  .editing-input::-webkit-input-placeholder {
    color: #858e98; }
  .editing-input:-ms-input-placeholder {
    color: #858e98; }
  .editing-input::-ms-input-placeholder {
    color: #858e98; }
  .editing-input::placeholder {
    color: #858e98; }
  .editing-input-title {
    font-family: HelveticaNeue-Bold;
    font-size: 13px;
    color: #000000;
    margin-bottom: 4px; }



