.diagram-list {
  padding-left: 25px;
  padding-top: 22px;
  margin-top: 117px;
  border-right: 1px solid #e9ecef;
  position: relative;
  min-height: calc(100vh - 117px); }
  @media screen and (max-width: 640px) {
    .diagram-list {
      margin-top: 137px;
      padding-left: 0px;
      padding-top: 0px;
      border-right: 0px;
      width: calc(181px + 1056px); } }
  @media screen and (max-width: 640px) {
    .diagram-list {
      min-height: calc(100vh - 137px); } }
  .diagram-list::after {
    position: absolute;
    content: " ";
    width: calc(1280px - 245px);
    height: 100%;
    background: #f6f7f9;
    left: 245px;
    top: 0px; }
    @media screen and (max-width: 640px) {
      .diagram-list::after {
        left: 181px; } }



