.mobile-divider {
  display: none; }
  @media screen and (max-width: 640px) {
    .mobile-divider {
      display: block;
      width: calc(100% + 16px + 16px);
      height: 8px;
      background: #e9ecef;
      position: relative;
      left: -16px;
      margin-bottom: 16px; } }

@media screen and (max-width: 640px) {
  .hidden-mobile {
    display: none; } }

@media screen and (min-width: 640px) {
  .visible-mobile {
    display: none; } }



